import React from 'react';
import s from './Footer.module.css';
import icon_1 from '../Images/ig_1.png'
import icon_2 from '../Images/vk-1.png'
import icon_3 from '../Images/fb_3.png'
import {NavLink} from "react-router-dom";


const Footer = () => {
    return (
        <div className={s.footer}>
            <div>☎ +375-29-803-0167
            <p>✉ Email: mig0687@rambler.ru</p>
            </div>
            <div>
                <div className={s.icons}>
                    <div className={s.icon}>
                        <a target="_blank" href="https://vk.com/id31433003"><img
                            src={icon_2} width="50" height="50" alt="vk.com"/></a>
                    </div>
                    <div className={s.icon}>
                        <a target="_blank" href="https://www.instagram.com/vsegdahorocha/?hl=ru"><img
                            src={icon_1} width="50" height="50" alt="instagram.com"/></a>
                    </div>
                    <div className={s.icon}>
                        <a target="_blank" href="https://fb.com"><img
                            src={icon_3} width="50" height="50" alt="fb.com"/></a>
                    </div>

                </div>
            </div>
            <div>
                🔎  Беларусь
                <p>г. Жабинка</p>
            </div>
            <div>
                <div className={s.ithem}>
                    <NavLink to={'/about'} activeClassName={s.activeLink}>О Бренде</NavLink>
                </div>
            </div>


        </div>
    );
}

export default Footer;