import React from 'react';
import s from './Collection.module.css'
import {BrowserRouter, HashRouter, NavLink} from "react-router-dom";
import {Redirect, Route} from "react-router";
import EvenDress from "./Evening/EvenDress";
import Everyday from "./Everyday/Everyday";
import Combi from "./Combi/Combi";
import EvenDressContainer from "./Evening/EvenDress";
import Deal from "../../Deal/Deal";


const Collection = () => {
    return (
        <div className={s.collection}>
            <div className={s.navMenu}>
                <div className={s.ithem}>
                    <NavLink to={'/evening'} activeClassName={s.activeLink}>Вечерние платья</NavLink>
                </div>
                <div className={s.ithem}>
                    <NavLink to={'/everyday'} activeClassName={s.activeLink}>Повседневные платья</NavLink>
                </div>
                <div className={s.ithem}>
                    <NavLink to={'/combi'} activeClassName={s.activeLink}>Комбинезоны</NavLink>
                </div>

            </div>

            <div className={s.dressContent}>
               {/* <Route path='/collection' />  <Redirect to={'/evening'} />*/}
                <Route path='/evening' render={() => <EvenDressContainer /> } />
                <Route path='/everyday' render={() => <Everyday /> } />
                <Route path='/combi' render={() => <Combi /> } />


            </div>
        </div>
    );
}

const ContainerColection = () => {
    return(
        <BrowserRouter>
            <Collection />
        </BrowserRouter>
    )
}

export default ContainerColection;