import React from 'react';
import s from './Combi.module.css';
import combi_1 from '../../../Images/Combi_1.jpg';
import combi_2 from '../../../Images/Combi_2.jpg';
import combi_3 from '../../../Images/Combi_3.jpg';
import combi_4 from '../../../Images/Combi_4.jpg';


const Combi = () => {
    return (
        <div className={s.combi}>
            <div className={s.line_1}>
                <div className={s.combiE}>
                    <img src={combi_4} alt="Dress" width={500} />
                </div>
                <div className={s.combiE}>
                    <img src={combi_2} alt="Dress" width={500}/>
                </div>
            </div>
            <div className={s.line_2}>
                <div className={s.combiE}>
                    <img src={combi_3} alt="Dress" width={500} />
                </div>
                <div className={s.combiE}>
                    <img src={combi_1} alt="Dress" width={500}/>
                </div>
            </div>


        </div>
    );
}

export default Combi;