import React from 'react';
import s from './Parcel.module.css';
import driver from '../../Images/Deliver.jpg';


const Parcel = () => {
    return (
        <div className={s.parcel}>
            <div className={s.drivery}>
                <img src={driver} alt="Parcel"/>
            </div>

        </div>
    );
}

export default Parcel;