import React from 'react';
import s from './Deal.module.css';
import CarouselBox_1 from "../component/CaruselBox_1";



const Deal = () => {
    return (
        <div className={s.deal}>
            <div >
                <p className={s.dressName}>Платье-трансформер изо льна</p>
                <div className={s.property}>
                <p>Состав: <span className={s.structure}>65% лен  35% хлопок</span></p>
                <p>Сезон: <span className={s.structure}>Весна-лето</span></p>
                <p>Описание:</p>
                <p>Льняное платье с длинными завязками. Специально умягченный лён, чтобы платье мялось,
                    как можно меньше и было приятно к телу.
                    Платье с секретом - на плече есть потайная пуговица,
                    чтобы сделать рукав 3/4 и красивый плечевой рельеф.</p>
                </div>
                {/*<img src={dressE_1} alt="" width={800}/>*/}
                <div className={s.content}>
                    <CarouselBox_1 />
                </div>

                <ul className={s.zice}>Размеры:
                    <li>S</li>
                    <li>Длина спереди: 125 см</li>
                    <li> Длина по середине спинки: 124 см</li>
                    <li>Длина рукава по плечевому шву: 69 см</li>
                    <li>Ширина рукава: 30 см</li>
                    <li>Ширина по низу рукава: 13 см</li>
                    <li>Ширина по низу: 63 см</li>
                </ul>
            </div>

        </div>
    );
}

export default Deal;