import React, {useState} from 'react';
import s from './Header.module.css'
import {NavLink} from "react-router-dom";
import Contacts from "../Content/Contacts/Contacts";


const Header = () => {

    let [editMode, setEditMode] = useState(false);
    /* state = {
         editMode: false,
     }*/
    const activateEditMode = () => {
        setEditMode(true);
    }

    const deactivateEditMode = () => {
        setEditMode(false);
    }

    return (
        <div className={s.header}>
            <div className={s.logo}>
                <NavLink to={'/def'} activeClassName={s.activeLink}>Def Tanja</NavLink>
            </div>

            <div className={s.nav}>
                <div className={s.ithem}>
                    <NavLink to={'/collection'} activeClassName={s.activeLink}>Коллекция</NavLink>
                </div>
                <div className={s.ithem}>
                    {!editMode &&
                    <div className={s.contacts}>
                        <span onClick={activateEditMode}>Контакты</span>
                    </div>
                    }
                    {editMode &&
                    <div className={s.number}>
                            <span  autofocus={true} onClick={deactivateEditMode}>
                                <p>Tel: +375-29-803-0167</p>
                                <p>Tel: +375-29-803-0167</p>
                            </span>
                    </div>
                    }

                    {/* <button onClick={handleClick}>Контакты</button>*/}
                    {/* <NavLink to={'/contacts'} activeClassName={s.activeLink}>Контакты</NavLink>*/}
                </div>
               {/* <div className={s.ithem}>
                    <NavLink to={'/about'} activeClassName={s.activeLink}>О Бренде</NavLink>
                </div>*/}
                <div className={s.ithem}>
                    <NavLink to={'/parcel'} activeClassName={s.activeLink}>Доставка</NavLink>
                </div>
                <div className={s.ithem}>
                    <NavLink to={'/sale'} activeClassName={s.activeLink}>Скидки</NavLink>
                </div>
               {/* <div className={s.ithem}>
                    <NavLink to={'/article'} activeClassName={s.activeLink}>Статьи</NavLink>
                </div>*/}

            </div>
        </div>
    );
}


export default Header;