import React from 'react';
import s from './AboutMe.module.css'
import brend from '../../Images/brend_2.png'

const AboutMe = () => {
    return (
        <div className={s.about}>
            <div className={s.brend}>
                <img src={brend} alt="brend"/>
            </div>

        </div>
    );
}

export default AboutMe;