import React, {Component} from "react";
import Carousel from "react-bootstrap/Carousel";
import img_1 from "../Images/Dr_1/I_1.jpg"
import img_2 from "../Images/Dr_1/I_2.jpg"
import img_3 from "../Images/Dr_1/I_3.jpg"
import img_4 from "../Images/Dr_1/I_4.jpg"





export default class CarouselBox_1 extends Component {
    render() {
        return (
            <Carousel fade>
                <Carousel.Item interval={3000}>
                    <img className="d-block w-100" src={img_1} alt="Def"/>
                    <Carousel.Caption>
                        <h3>Def Tanja</h3>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={3000}>
                    <img className="d-block w-100" src={img_2} alt="Def"/>
                    <Carousel.Caption>
                        <h3>Def Tanja</h3>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={3000}>
                    <img className="d-block w-100" src={img_3} alt="Def"/>
                    <Carousel.Caption>
                        <h3>Def Tanja</h3>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={3000}>
                    <img className="d-block w-100" src={img_4} alt="Def"/>
                    <Carousel.Caption>
                        <h3>Def Tanja</h3>
                    </Carousel.Caption>
                </Carousel.Item>


            </Carousel>
        )

    }
}