import React from 'react';
import s from './Everyday.module.css';
import dressD_1 from '../../../Images/DressDay_1.jpg';
import dressD_2 from '../../../Images/DressDay_2.jpg';
import dressD_3 from '../../../Images/DressDay_3.jpg';
import dressD_4 from '../../../Images/DressDay_4.jpg';


const Everyday = () => {
    return (
        <div className={s.everyday}>
            <div className={s.line_1}>
                <div className={s.dressE}>
                    <img src={dressD_1} alt="Dress" width={500} />
                </div>
                <div className={s.dressE}>
                    <img src={dressD_4} alt="Dress" width={500}/>
                </div>
            </div>
            <div className={s.line_2}>
                <div className={s.dressE}>
                    <img src={dressD_3} alt="Dress" width={500} />
                </div>
                <div className={s.dressE}>
                    <img src={dressD_2} alt="Dress" width={500}/>
                </div>
            </div>


        </div>
    );
}

export default Everyday;