import React from 'react';
import s from './Evening.module.css';
import dressE_1 from '../../../Images/Dr_1/I_1.jpg';
import dressE_2 from '../../../Images/DressEv_2.jpg';
import dressE_3 from '../../../Images/DressEv_3.jpg';
import dressE_4 from '../../../Images/DressEv_4.jpg';
import {BrowserRouter, HashRouter, NavLink} from "react-router-dom";
import {Route} from "react-router";
import Deal from "../../../Deal/Deal";
import Deal_3 from "../../../Deal/Deal_3";



const EvenDress = () => {
    return (
        <div className={s.evening}>
            <div className={s.dressContent}>
                <div className={s.dressE}>
                    <NavLink to={'/evening/dressE_1'}><img src={dressE_1} alt="Dress" width={500} /></NavLink>
                </div>
                <div className={s.dressE}>
                    <NavLink to={'/evening/dressE_3'}><img src={dressE_3} alt="Dress" width={500}/></NavLink>
                </div>
                <div className={s.dressE}>
                    <img src={dressE_2} alt="Dress" width={500} />
                </div>
                <div className={s.dressE}>
                    <img src={dressE_4} alt="Dress" width={500}/>
                </div>
            </div>

            <div className={s.dressRout}>

                <Route path='/evening/dressE_1' render={() => <Deal /> } />
                <Route path='/evening/dressE_3' render={() => <Deal_3 /> } />


            </div>

        </div>
    );
}

const EvenDressContainer = () => {
    return(
        <BrowserRouter>
            <EvenDress />
        </BrowserRouter>
    )
}

export default EvenDressContainer;