import React from 'react';
import './App.css';
import Header from "./Header/Header";
import Content from "./Content/Content";
import Footer from "./Footer/Footer";
import {Redirect, Route} from "react-router";
import AboutMe from "./Content/AboutMe/AboutMe";
import Parcel from "./Content/Parcel/Parcel";
import Sale from "./Content/Sale/Sale";
import Article from "./Content/Article/Article";
import ContainerColection from "./Content/Collection/Collection";



const App = () => {
    return (
        <div className="app-wrapper">
            <Header/>
            <div className="app-wrapper-content">
                <Route path='/' />  <Redirect to={'/def'} />

                <Route path='/def' render={() => <Content />} />
                <Route path='/collection' render={() => <ContainerColection />} />
                <Route path='/about' render={() => <AboutMe />} />
                <Route path='/parcel' render={() => <Parcel />} />
                <Route path='/sale' render={() => <Sale />} />
                <Route path='/article' render={() => <Article />} />

            </div>
            <Footer/>

        </div>
    );
}





export default App;
