import React from 'react';
import s from './Deal.module.css';
import CarouselBox_3 from "../component/CaruselBox_3";



const Deal_3 = () => {
    return (
        <div className={s.deal}>
            <div >
                <p className={s.dressName}>Платье-солнышко</p>
                <div className={s.property}>
                    <p>Состав: <span className={s.structure}>65% лен  35% хлопок</span></p>
                    <p>Сезон: <span className={s.structure}>Лето</span></p>
                    <p className={s.structure}>Описание:</p>
                    <p>Льняное платье. Специально умягченный лён, чтобы платье мялось,
                        как можно меньше и было приятно к телу.
                    </p>
                </div>

                {/*<img src={dressE_1} alt="" width={800}/>*/}
                <div className={s.content}>
                    <CarouselBox_3 />
                </div>

                <ul className={s.zice}>Размеры:
                    <li>S</li>
                    <li>Длина спереди: 125 см</li>
                    <li> Длина по середине спинки: 124 см</li>
                    <li>Длина рукава по плечевому шву: 69 см</li>
                    <li>Ширина рукава: 30 см</li>
                    <li>Ширина по низу рукава: 13 см</li>
                    <li>Ширина по низу: 63 см</li>
                </ul>
            </div>

        </div>
    );
}

export default Deal_3;